}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 1s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  
  border-radius: 10px;
  border-style: solid;
  border-color:red;
  
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}


